import Joi from 'joi'
import chalk from 'chalk'

const defaultValue = ['static']
let propType = {}

const isIsomorphic = process.env.ENABLED_ISOMORPHIC === 'true'

if (isIsomorphic) {
	propType = {
		staticDirs: Joi.array().items(Joi.string().trim().min(1).required()).min(1).required()
	}
} else {
	propType = {
		staticDirs: Joi.array().items(Joi.string().trim().min(1).required()).min(1)
	}
}

export default (staticDirs, getDefaultValue = false) => {
	if (getDefaultValue) {
		return defaultValue
	}
	const {error, value} = Joi.validate({staticDirs}, propType)
	if (error !== null) {
		console.error(error.message)
		console.log(chalk.bold.yellow(`Current value: "${staticDirs}"`))
		console.log(chalk.bold.cyan(`We are using: "${defaultValue}"`))
		return defaultValue
	}
	return value.staticDirs
}